@import 'colors';

.d-flex{
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
}
.d-block{
	display: block;
}
.cn-between{
	justify-content: space-between !important;
}
.cn-center{
	justify-content: center;
}
.ps-absolute{
	position: absolute;
}
.width-100{
	width: 100%;
}
.right-0{
	right: 0;
}
.top-0{
	top: 0;
}
.bottom-0{
	bottom: 0;
}
.left-0{
	left: 0;
}